.department{
    
    figure{
        width: 100%;
        height: 75vh;
        position: relative; 
        margin-top: -100px;
        overflow: hidden;
    
        // video{
        //     width:100%;
        //     height:100%;
        //     object-fit: cover;
        // }

        img{
            width:100%;
            height:100%;
            object-fit: cover;
            filter:saturate(20%);
        }
        
        .aboutBg{
            font:bold 500px/1 'Orbitron';
            color:rgba(255,255,255,.09);
            letter-spacing: 25px;
            position: absolute;
            top: 40%;
            left: 75%;
            transform: translate(-50%,-50%);
            font-style: italic;

            strong{
                position: relative;
                top: 100px;

                b{
                    position: relative;
                    top: -75px;
                    color:rgba(255,255,255,.15);
                    text-shadow: 5px 5px 10px rgba(255,255,255,.1);
                }
            }
        }
    
        .aboutContent{
            position: absolute;
            top: 60%;
            // left: 36.5%;
            left: 75%;
            transform: translate(-50%,-50%);
            width: 35%;
            // border:1px solid red;

            h1{
                font:bold 65px/1 'Orbitron';
                color:#fff;
                letter-spacing: 10px;
                margin-bottom: 20px;
                text-shadow: 5px 5px 7px rgba(0,0,0,.5);
            }

            button{
                width: 170px;
                height: 45px;
                background: #111;
                font:normal 11px/1 'Orbitron';
                color:#fff;
                letter-spacing: 1.2px;
                margin-bottom: 100px;
                cursor: pointer;
            }
        
            b{
                display: block;
                font:bold 12px/1 'Orbitron';
                color:#eee;
                letter-spacing: 1.7px;
            }
        }

        .aboutList{
            position: absolute;
            top: 60%;
            left: 20.25%;
            transform: translate(-50%,-50%);

            >ul{

                li{
                    font:normal 20px/1 'Orbitron';
                    color:#fff;
                    letter-spacing: 1.2px;
                    text-align: center;
                    
                    >button{
                        width: 30px;
                        height: 30px;
                        background: #111;
                        color:#fff;
                        font-size: 20px;
                        margin-top: 30px;
                        margin-bottom: 50px;
                        cursor: pointer;
                    }

                    >b{
                        font:bold 35px/1 'Orbitron';
                        color:#fff;
                        letter-spacing: 1.2px;
                    }
                }
            }
        }
    }
}


