#member{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/memberBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 150px 0px;
    border:1px solid red;

    .inner{
        width: 100%;
        margin: 0 auto;

        h1{
            font:bold 22px/1 'arial';
            color:#333;
            text-align: center;
            margin-bottom: 50px;
        }

        .wrap{
            width: 100%;
            height: 700px;
            padding:100px 0px;
        }
    }
}