@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Playfair+Display:ital@0;1&display=swap');

.gallery {

    figure{
      width: 100%;
      min-height: 3900px;
      position: relative; 
      margin-top: -100px;
      background-image: url(../../../public/img/gallery06.jpg);
      background-size: cover;
      background-position: bottom;
      background-repeat: no-repeat;
      filter: saturate(50%);
      overflow: hidden;

    .galleryMian{
      position: absolute;
      top: 3.5%;
      left: 50%;
      transform: translateX(-50%);
      width: 1180px;
      padding:50px;
      // box-shadow: 5px 5px 10px rgba(255,255,255,.7);
      box-shadow: 5px 5px 10px rgba(0,0,0,.7);

           
      video{
        width: 100%;
        height: 700px;
        object-fit: cover;
        filter: saturate(30%);
      }

      >div{
        width: 45%;
        position: absolute;
        top: 50px;
        padding:25px;

        h1{
          font:bold 75px/1 'Orbitron';
          color:rgba(255,255,255,.9);
          text-shadow: 5px 5px 10px rgba(255,255,255,.2);
          letter-spacing: 5px;
          margin-bottom: 60px;
          position: relative;

          &::before{
            content: '';
            display: block;
            width: 100%;
            height: 3px;
            background: #fff;
            position: absolute;
            bottom: -30px;
            right: 30%;
          }
        }
    
        p{
          font:normal 10px/15px 'Orbitron';
          color:rgba(255,255,255,.3);
          letter-spacing: 1px;
        }
  
          
        strong{
          font:bold 10px/1 'Orbitron';
          color:rgba(255,255,255,.5);
          letter-spacing: 1.2px;
          margin-top: 30px;
          display: block;
        }
  
      }

    }
  
    .picList{
      width: 100%;
      min-height: 1100px;
      position: relative; 
      top: 25%;
      left: 0;
      // background: rgba(0,0,0,.5);
      // background: rgba(255,255,255,.02);

      .subTitle{
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        font:bold 50px/1 'Orbitron';
        color:rgba(255,255,255,.9);
        letter-spacing: 5px;

        &::after{
          content: '';
          display: block;
          width: 50%;
          height: 2.5px;
          background: #fff;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 105%;
        }
      }

      .pics{
        position: absolute;
        top:0%;
        left: 0%;
        width: 50%;
        height: 100%;
        // border:1px solid red;
    
        h2{
          position: relative;
          bottom: -400px;
          left: -35%;
          font:bold 100px/1 'Orbitron';
          color:rgba(255,255,255,.1);
          letter-spacing: 5px;
          transform: rotate(270deg);
      
          b{
            font-size: 150px;
            line-height: 75px;
          }
        }
    
        .pic01{
          width:350px;
          height: 500px;
          position: relative;
          top: 0;
          left: 70%;
        
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            filter: saturate(70%);
          }
        }
    
        .pic02{
          width: 450px;
          height: 600px;
          position: relative;
          top: -375px;
          left: 30%;
        
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            filter: saturate(70%);
          }
        }
    
        .pic03{
          width: 350px;
          height: 450px;
          position: relative;
          top: -580px;
          left: 70%;
        
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            filter: saturate(70%);
          }
        }
      }

      .gallerySlider{
          position: absolute;
          top: 0;
          right: 0;
          width: 45%;
          height: 100%;
          padding:5% 0%;
          overflow: hidden;
          // filter: saturate(100%);
  
          li{
              width: 100%;
              height: 100%;
              float: left;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              padding:100px;
  
              img{
                  width: 100%;
                  height: 50%;
                  object-fit: cover;
              }
              
              .contents{
                  width: 100%;
                  height: 50%;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  flex-wrap: wrap;
                  text-align: center;
                  background: rgba(255,255,255,.1);
  
                  h4{
                      font:bold 16px/1 'Orbitron';
                      color:#111;
                      letter-spacing: 2px;
                      margin-bottom: 30px;
                      width: 80%;
                  }
  
                  p{
                      font:normal 11px/15px 'Orbitron';
                      color:#333;
                      letter-spacing: 1.3px;
                      margin-bottom: 15px;
                      width: 80%;
                  }
                  span{
                      font:bold 10px/1 'Orbitron';
                      color:#333;
                      letter-spacing: 1.1px;
                      margin-bottom: 30px;
                  }
  
                  .pages{
                      display: flex;
                      flex-direction: column;
                      width: 20%;
                      
                      b{
                          font:bold 10px/1 'Orbitron';
                          color:#333;
                          letter-spacing: 1.1px;
  
  
                          &:nth-child(1){
                              margin-bottom: 10px;
                              text-align: left;
                          }
  
                          &:nth-child(3){
                              text-align: right;
                          }
                      }
  
                      >div{
                          width: 100%;
                          height: 2px;
                          background: #000;
                          margin-bottom: 10px;
                      
                    }
                }
            }
        }
      }
          
      .prev{
        position: absolute;
        top: 30%;
        left: 56%;
        transform: translateY(-50%);
        font:bold 30px/1 'Orbitron';
        color:#555;
        cursor: pointer;
        border:none;
        background:none;
      }

    .next{
        position: absolute;
        top: 70%;
        left: 56%;
        transform: translateY(-50%);
        font:bold 30px/1 'Orbitron';
        color:#555;
        cursor: pointer;
        border:none;
        background:none;
      }
    }
  }

  .test{
    width: 300px;
    height: 250px;
    object-fit: cover;
    position: absolute;
    top: 52.5%;
    left: 5%;
    opacity: 0.3;
    transform: rotate(-30deg);
  }

  .test2{
    width: 300px;
    height: 250px;
    object-fit: cover;
    position: absolute;
    top: 51%;
    right: -5%;
    opacity: 0.7;
    // transform: rotate(-30deg);
  }

  h3{
    font:bold 100px/1 'Orbitron';
    color:rgba(255,255,255,.09);
    letter-spacing: 5px;
    position: absolute;
    top: 55.5%;
    left: 35%;

    // &::before{
    //   content: '';
    //   display: block;
    //   width: 160%;
    //   height: 75px;
    //   // background: rgba(0,0,0,.01);
    //   border-right:5px solid rgba(255,255,255,.1);
    //   border-top:5px solid rgba(255,255,255,.1);
    //   position: absolute;
    //   top: 12%;
    //   transform: translateY(-50%);
    //   right:-7.5%;
    // }

    // &::after{
    //   content: '';
    //   display: block;
    //   width: 50px;
    //   height: 5px;
    //   background: rgba(255,255,255,.1);
    //   position: absolute;
    //   top: 50%;
    //   transform: translateY(-50%);
    //   right:-7.05%;
    // }
  }

    .innerWrap {
      transform: translateY(200px);
      opacity: 0;
      transition: 1s;

      >.inner {
        transition: none;
        width: 1180px;
        margin: 0px auto;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 1200px;
        min-height:  1000px;
        border:1px solid rgba(0,0,0, .01);

        .searchBox{
          position: absolute;
          top: 25px;
          right: 10px;

          input[type=text]{
            width: 167px;
            height: 35px;
            font-family: 'Orbitron';
            font-size: 10px;
            padding-left:5px;
            letter-spacing: 1.2px;
          }

          button{
            width: 50px;
            height: 35px;
            font-family: 'Orbitron';
            color:#fff;
            font-size: 10px;
            background: rgba(0,0,0,.5);
            border:none;
            cursor: pointer;
            letter-spacing: 1.2px;
          }
        }

        .loading {
          position: absolute;
          top:0px;
          left: 50%;
          width: 50%;
          transform: translateX(-50%);
        }

        >section {
          transform: translateY(200px);
          opacity: 0;
          transition: 1s;
          margin-top: 75px;
          margin-bottom:50px;
    
          &.on {
            transform: translateY(0px);
            opacity: 1;
          }
    
          &::after {
            content: '';
            display: block;
            clear: both;
          }
    
          article {
            width: 20%;
            float: left;
            padding: 10px;
    
            >.inner {
              width: 100%;
              height: 100%;
              background: #fff;
              box-shadow: 5px 5px 10px rgba(0,0,0, .1);
              padding: 10px;
    
              .pic {
                width: 100%;
                margin-bottom: 10px;
                cursor: pointer;
    
                img {
                  width: 100%; 
                }            
              }
    
              >b {
                font:normal 10px/1 'Orbitron';
                color:#555;
                letter-spacing: 2.5px;
                margin-bottom: 20px;
              }
            }
          }
        }
      } 
    } 
    
    &.on {
      .innerWrap {
        transform: translateY(0px); opacity: 1;
      }
    }
  }
  
  .popup {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 5vw;
    z-index: 6;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  
    span {
      font: bold 14px/1 'arial';
      color: #fff;
      position: absolute;
      top: 4vh;
      right: 5vw; cursor: pointer;
    }
  }
  
  @media screen and (max-width: $tablet){
    .content {
      >figure {
        height: 200px;
      }
  
      .innerWrap {
        >.inner {
          width: 100%;
          padding: 0px 3vw;
          padding-top: 80px
        }
      }
      
    }
  }
  
  @media screen and (max-width: 1179px){
    .gallery {
      .item {width: 25%!important;}
    }
  }
  
  @media screen and (max-width: 899px){
    .gallery {
      .item {width: 33.333%!important;}
    }
  }
  @media screen and (max-width: 739px){
    .gallery {
      .item {width: 50%!important;}
    }
  }
  @media screen and (max-width: 479px){
    .gallery {
      .item {width: 100%!important;}
    }
  }
 