.join{
    margin-top:-100px;
    min-height: 1250px;
    padding:150px 0px;
    position: relative;
    overflow: hidden;
    background: #000;

    video{
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        filter: saturate(25%);
        opacity: 0;
    }

    img{
        object-fit: contain;
        position: absolute;
        z-index:1;
        bottom: -100%;

        &:nth-of-type(1){
            left: 10%;
            width: 90px;
            height: 90px;
            animation: joinAni 30s linear infinite;
        }

        &:nth-of-type(2){
            left: 25%;
            width: 100px;
            height: 100px;
            animation: joinAni2 30s  5s linear infinite;
        }

        &:nth-of-type(3){
            left: 50%;
            width: 75px;
            height: 75px;
            animation: joinAni3 40s 7s linear infinite;
        }

        &:nth-of-type(4){
            left: 75%;
            width: 65px;
            height: 65px;
            animation: joinAni4 35s  4s linear infinite;
        }

        &:nth-of-type(5){
            left: 90%;
            width: 120px;
            height: 120px;
            animation: joinAni5 30s 2s linear infinite;
        }
    }

