    .inner{
        
        .joinTitle{
            // background: rgba(255,255,255,.07);
            background: rgba(255,255,255,.09);
            min-height: 300px;
            position: relative;
            
            .joinSns{
                width: 15%;
                display: flex;
                justify-content: space-around;
                position: absolute;
                right: 10px;
                top: 20px;
                color:#fff;
                cursor: pointer;
                font-size: 22.5px;
            }

            .joinContent{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                width: 80%;
                position: absolute;
                left: 10%;
                top: 10%;
                padding:15px 25px;

                &::before{
                    content: '';
                    display: block;
                    width: 5px;
                    height: 100%;
                    background: #f9bf7c;
                    position: absolute;
                    top: 0;
                    left: -6.5%;
                }

                h1{
                    font:bold 45px/1 'Orbitron';
                    color:#f9bf7cc9;
                    letter-spacing: 2.3px;
                    margin-bottom: 30px;
                }

                p{
                    font:normal 10px/15px 'Orbitron';
                    color:#eee;
                    letter-spacing: 1.3px;
                    width: 50%;
                    margin-bottom: 30px;
                }

                span{
                    font:bold 10px/13px 'Orbitron';
                    color:#eee;
                    letter-spacing: 1.3px;
                    margin-bottom: 15px;
                }

                button{
                    width: 120px;
                    height: 30px;
                    border:none;
                    background: #f9bf7c;
                    font:bold 11px/13px 'Orbitron';
                    color:#333;
                    letter-spacing: 1.3px;
                    cursor: pointer;
                    transition: 1s;

                    &:hover{
                        background: #fff;
                        color:#111;
                    }
                }
            }
        }

        .joinForm{
            background: #fff;
            padding-bottom: 0;

            .success{
                width: 100%;
                padding: 20px;
                background: lightcyan;
                font:bold 16px/1 '맑은 고딕';
                color:cornflowerblue;
                margin-bottom: 50px;
            }
            
            table{
                width: 80%;
                margin-left: 10%;

                tbody{
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                }

                th,td{
                    border-bottom: 1px solid #ddd;
                    padding:50px 30px 15px;
                }

                .comment{
   
                    th{
                        line-height: 150px;
                        padding:30px;
                    }


                    td{
                        width: 770px;
                        padding:30px;

                        textarea{
                            width: 100%;
                            height: 150px;
                            padding:10px 20px;
                            border:1px solid #aaa;
                            resize: none;
                        }
                    }
                }
            
                th{
                    width: 170px;
                    height:130px;
                    text-align: left;
                    font:bold 10px/1 'Orbitron';
                    color:#555;
                    letter-spacing: 1px;
                    text-align: center;
                    line-height:35px;
            
                    &.btnSet{
                        text-align: center;
                        width: 940px;
                        padding:0px 30px;
                        height: 100px;
                        line-height: 100px;

                        input[type=reset], input[type=submit]{
                            width: 150px;
                            height: 40px;
                            text-align: center;
                            margin: 30px;
                            cursor: pointer;
                            transition: 0.5s;
                            border:none;
                            background: #f9bf7c;
                            font:bold 11px/13px 'Orbitron';
                            color:#333;
                            letter-spacing: 1.5px;
            
                            &:hover{
                                background: #555;
                                color:#fff;
                            }
                        }
                    }
                }

                td{
                    width: 300px;

                    input[type=text], input[type=password],select{
                        width: 100%;
                        padding:7px 20px;
                        border:1px solid #aaa;
                    }
        
                    label{
                        font:12px/1 'arial';
                        color:#777;
                        margin-right: 5px;
                    }
            
                    input[type=checkbox], input[type=radio]{
                        margin-right: 20px;
                    }
            
                    .err{
                        font:bold 10px/13px 'Orbitron';
                        color:hotpink;
                        display: block;
                        margin-top:5px;
                        letter-spacing: 1px;
                    }
                }
            }
        }
    }
}

@keyframes joinAni{
    0%{bottom: -200%; left: 10%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.5;}
    50%{opacity: 0.5; }
    75%{bottom: 100%; left: 25%; opacity: 0;}
    100%{bottom:0%;   opacity: 0;}
}

@keyframes joinAni2{
    0%{bottom: -200%; left: 25%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.1;}
    50%{opacity: 0.1;}
    75%{bottom: 80%;  left: 50%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes joinAni3{
    0%{bottom: -200%; left: 50%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.2;}
    50%{opacity: 0.2; }
    75%{bottom: 120%; left: 35%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes joinAni4{
    0%{bottom: -200%; left: 75%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.4;}
    50%{opacity: 0.4; }
    75%{bottom: 100%; left: 60%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}

@keyframes joinAni5{
    0%{bottom: -200%; left: 90%; opacity: 0;}
    25%{bottom: 0%; opacity: 0.3;}
    50%{opacity: 0.3;}
    75%{bottom: 70%;  left: 80%; opacity: 0;}
    100%{bottom:0%; opacity: 0;}
}
