#visualFigure{
    width: 100%;
    height: 200vh;
    position: relative;
    background-image: url(../../../public/img/visualBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);

    >img{
        position: absolute;
        width: 300px;
        height: 300px;
        object-fit: cover;
        top: 25%;
        left: 62.5%;
        transition: all 3s;
    }

    >section{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        // border:3px solid blue;

        >.inner{
            width: 1180px;
            height: 100%;
            margin: 0 auto;
            position: relative;

            h2{
                font:bold 35px/1 'Orbitron';
                color:rgba(255,255,255,.3);
                letter-spacing: 1.3px;
                position: absolute;
                top:30%;
                left: -5%;

                &:nth-child(1){
                    top: 27%;
                }

                &:nth-child(2){
                    left: 0;
                }
            }

            h3{
                font:bold 100px/1 'Orbitron';
                color:rgba(255,255,255,.7);
                letter-spacing: 5px;
                position: absolute;
                top:33%;
                left: 0;
            }

            p{
                font:normal 13px/1 'Orbitron';
                color:rgba(255,255,255,.6);
                letter-spacing: 2.3px;
                position: absolute;
                top:40%;
                left: 30%;
            }

            b{
                font:bold 13px/1 'Orbitron';
                color:rgba(255,255,255,.6);
                letter-spacing: 2.3px;
                position: absolute;
                top:41.5%;
                left: 30%;
                span{
                    font-weight: normal;
                }
            }
        }

        .page01{
            position: absolute;
            top: 49%;
            transform: translateY(-50%);
            left: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            width: 100%;
            padding:0px 30px;

            strong{
                font:bold 15px/1 'Orbitron';
                color:rgba(255,255,255,.9);
                letter-spacing: 3px;
            }

            ul{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width:150px;
                li{
                    cursor: pointer;
                    font:bold 15px/1 'Orbitron';
                    color:rgba(255,255,255,.9);
                    letter-spacing: 3px;
                }
            }
        }
    }
    // .swiper{
