.youtube{
    background: #efefef;

    figure{
      width: 100%;
      min-height: 3900px;
      position: relative; 
      margin-top: -100px;
      background-image: url(../../../public/img/youtube01.jpg);
      background-size: cover;
      background-position: left bottom;
      background-repeat: no-repeat;
      filter: saturate(50%);
      overflow: hidden;

        // video{
        //     width:100%;
        //     height:100%;
        //     object-fit: cover;
        // }

        // >img{
        //     width:100%;
        //     height:100%;
        //     object-fit: cover;
        //     filter: saturate(30%);
        // }

        .explanation01{
            position: absolute;
            left: 1550px;
            top: 9.5%;
            width: 200px;
            height: 200px;

            .exP{
                font:normal 10px 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 1.2px;
                color:#fff;
                width: 200px;
                height: 100%;
                display: block;
                position: absolute;
                transform: rotate(90deg);

                &::before{
                    content: '';
                    display: block;
                    width: 2px;
                    height: 80%;
                    background: #fff;
                    position: absolute;
                    bottom:45%;
                    right:20%;
                    transform: rotate(90deg);
                    }

                    &::after{
                        content: '∨';
                        display: block;
                        font-size: 20px;
                        color:#fff;
                        position: absolute;
                        bottom:78.5%;
                        right:-22%;
                        transform: rotate(270deg);
                        }
            }
        }

        .box{
            position: absolute;
            top: 4.5%;
            left: 35%;
            border-top:3px solid #fff;
            border-left:3px solid #fff;
            border-bottom:3px solid #fff;
            // background: #000;
            width: 320px;
            height: 370px;
        }

        >div{
            width: 1180px;
            position: relative;
            height: 15%;
            left: 50%;
            top: 5%;
            transform: translateX(-50%);
            display: flex;
            justify-content: space-evenly;
            align-items: flex-start;
            padding:0px 150px;

            h1{
                font:normal 75px 'Orbitron';
                color:rgba(255,255,255,.8);
                letter-spacing: 5px;
                position: relative;

                &::before{
                content: '';
                display: block;
                width: 3px;
                height: 35px;
                background: #fff;
                position: absolute;
                top: -6%;
                right:9.5%;
                }

                &::after{
                content: '';
                display: block;
                width: 3px;
                height: 35px;
                background: #fff;
                position: absolute;
                bottom: -9%;
                right:9.5%;
                }
    
                span{
                    display: block;
                    padding-top: 20px;
                    padding-bottom: 20px;
                }
    
                b{
                    color:#fff;
                }
            } 
    
            p{
                font:normal 11px/20px 'Orbitron';
                color:#ddd;
                letter-spacing: 1.3px;
                margin-bottom: 30px;
                padding-top:35px;
            }
        }
        
        .subvideo{
            position: relative;
            width: 100%;
            height: 250px;
            top: 2.5%;
            filter: saturate(50%);
            z-index:3;

            .swiper{
                width: 100%;
                height: 100%;
                
                .swiper-button-next{
                    color:rgba(255,255,255,.6);
                    transition: 1s;

                    &::after{
                        content: '＞';
                    }

                    &:hover{
                        color:#fff;
                    }
                }

                .swiper-button-prev{
                    color:rgba(255,255,255,.6);
                    transition: 1s;

                    &::after{
                        content: '＜';
                    }

                    &:hover{
                        color:#fff;
                    }
                }

                >.swiper-wrapper{

                    .swiper-slide{
                        width: 100%;
                        height: 100%;
    
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                }


            }
        }
    }
    

    
    .inner{
        width: 1180px;
        position: relative;
        padding-top: 125px;
        border:1px solid red;
        height: 100%;

        h2{
            font:bold 50px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            letter-spacing: 5px;
            position: absolute;
            top: 25px;
        }

        >section{
            padding-bottom: 100px;

            &::after{
                content: '';
                display: block;
                clear: both;
            }

            article{
                width: 50%;
                height: 500px;
                float: left;
                padding:10px;
                overflow: hidden;
                margin-bottom: 20px;
                
                .inner{
                    width: 100%;
                    height: 100%;
                    background: #fff;
                    box-shadow: 3px 3px 10px rgba(0,0,0,.1);
                    padding:10px;
                    display: flex;
                    flex-wrap: wrap;
                    align-content: space-between;
                    justify-content: center;

                    /*
                    word-break : 텍스트의 줄바꿈 속성 지정
                    - normal : 한글은 한글자 기준, 영어는 단어를 기준으로 줄바꿈 됨(default)
                    - break-all : 한글과 영어 모두 글자 단위로 줄바꿈
                    - keep-all : 한글, 영어 모두 단어 기준으로 줄바꿈
                    */

                    .txt{

                        h2{
                            font:bold 16px/1.2 'arial';
                            margin-bottom: 20px;
                            color:#555;
                        }

                        p{
                            font:12px/1.2 'arial';
                            color:#888;
                        }
                    }


                    .pic{
                        width: 100%;
                        height: 150px;
                        cursor: pointer;
                        overflow: hidden;

                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            transform: scale(1.6);
                        }
                    }

                }
            }
        }
    }
}

.pop{
    width: 100%;
    height: 90%;
    background: #000;
    position: fixed;
    top: 5%;
    left: 0;
    padding:50px;

    iframe{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    span{
        position: absolute;
        color:#fff;
        font:bold 13px/1 'arial';
        top: 20px;
        right: 20px;
        cursor: pointer;
    }
}